.games-add-result-who-players {
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding: 10px;
}

.games-add-result-who-player {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
    background-color: #0000003e;
    color: white;
    padding: 10px;
    cursor: pointer;

    font-size: larger;
    box-sizing: border-box;

    $bg-color: rgb(230, 230, 230);
    border-radius: 10px;
    &:hover, &.selected {
        background-color: #e5e5f7;
        background:
            linear-gradient(to right, $bg-color 4px, transparent 4px) 0 0,
            linear-gradient(to right, $bg-color 4px, transparent 4px) 0 100%,
            linear-gradient(to left, $bg-color 4px, transparent 4px) 100% 0,
            linear-gradient(to left, $bg-color 4px, transparent 4px) 100% 100%,
            linear-gradient(to bottom, $bg-color 4px, transparent 4px) 0 0,
            linear-gradient(to bottom, $bg-color 4px, transparent 4px) 100% 0,
            linear-gradient(to top, $bg-color 4px, transparent 4px) 0 100%,
            linear-gradient(to top, $bg-color 4px, transparent 4px) 100% 100%;
        border-radius: 0px;
        
        background-repeat: no-repeat;
        background-size: 20px 20px;
        box-shadow: 0 0 10px rgba(235, 235, 235, 0.9);
    }
}